<template>
    <div class="container content-2" >
        <div class="text-center mb-5">
            <h3>Expertise</h3>
        </div>
        <div class="row mt-5 mb-5">
            <div class="col-md-6" v-scrollanimation>
                <div class="d-flex flex-column align-items-center container">
                    <div class="list-content" v-for="item in this.objects[0].frontend" v-bind:key="item.id" v-scrollanimation>
                        <section class="text-inner">{{ item.skill_name }}</section>
                    </div>
                </div>
            </div>
            <div class="col-md-6 mb-5">
                <content-child :backend="objects" />
            </div>
        </div>
    </div>
</template>

<script>
import Items from '../../items.json'
import ContentChild from './ContentChild';
export default {
    name: 'Content',
    components: {
        contentChild: ContentChild
    },
    data() {
        return {
            items: Items,
            objects: []
        }
    },
    created() {
        const contents = []

        for(let i = 0; i < this.items.data.length; i++){
            contents.push(this.items.data[i])
        }

       const res = JSON.parse(JSON.stringify(contents))
       this.objects = res
       console.log(this.objects)
    }
}
</script>

<style scoped>

    .image-body {
        position: absolute;

    }

    .before-enter {
        transform: translateX(-50px);
        transition: all 2s cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    .enter {
        transform: translateX(0px);
    }

    .list-content {
        background: #dbe5fe;
        display: inline-block;
        width: 100%;
        text-align: center;
        padding: 15px;
        transition-delay: .1s;
    }

    .text-inner {
        background: #3756a8;
        padding: 12px;
        color: white;
        font-size: 20px;
    }
    

</style>